import React from "react"
import { graphql, Link } from "gatsby"

import { MDXProvider } from "@mdx-js/react"
import { MDXRenderer } from "gatsby-plugin-mdx"

import classNames from "classnames";
import LazyHero from 'react-lazy-hero';

import Layout from "@components/layout/layout"

import { makeStyles } from "@material-ui/core/styles";

import { postStyle } from "assets/jss/template/blog/post.js";

// MDX Components
import { H1, H2, H3, H4, H5, H6 } from "@components/header/header.js";
import Button from "@components/customButtons/button.js";
import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";

// Provide common components here
const shortcodes = { GridContainer, GridItem, Button, Link, H1, H2, H3, H4, H5, H6 }



const useStyles = makeStyles(postStyle);

/*
children: 
location: {hash:, host:, hostname:, href, key, orgin, pathname, port, protocol, search, state:{key:}}
navigate:  navigate(to, options)
pageContext: {slug: }
pageResources: {json: {}, page: {}, staticQueryResults: {}, component: }
params: {}
path: "/about"
pageContext: {slug: "/about"} //Based off gatsby-node create page
uri: "/about"
*/
export default function TemplatePost({ data, pageContext }) {
  
  const { node } = data  
  const classes = useStyles();

  function GetHeroClassNames() {
    const heroClassNames = [classes.hero];
    if (node.context.pageStyle === null) {
      return classNames(heroClassNames)
    }

    if (node.context.pageStyle[0].name === "hero") {
      if (node.context.pageStyle[0].style === null) {
        return classNames(heroClassNames)
      }
      return classNames(heroClassNames.concat(node.context.pageStyle[0].style));
    }
    for (let index = 0; index < node.pageStyle.length; index++) {
      if (node.context.pageStyle[index].name === "hero") {
        return classNames(heroClassNames.concat(node.context.pageStyle[index].style));
      }
    }

    return classNames(heroClassNames)

  }

  return (
    <Layout pageNode={node}>
      <LazyHero className={GetHeroClassNames()} imageSrc={node.context.hero.publicURL}>
        <div>
          <H1>{node.context.title}</H1>
          <H4>Part 1</H4>
          <H6>#3MysticApes{node.context.series === null ? "" : node.context.series}</H6>
        </div>
      </LazyHero>
      <MDXProvider components={shortcodes}>
        <MDXRenderer classes={classes}>{pageContext.body}</MDXRenderer>
      </MDXProvider>
    </Layout>
  )
}

// node:threeMysticApesPost(post: {slug: {eq: $rawSlug}}) {
export const query = graphql`
 query($postID: String!) {
    node:threeMysticApesPost(id: {eq: $postID}) {
      context: post {
        title
        description
        keywords
        meta {
          content
          name
          property
        }
        mysticApe
        series
        pageStyle {
          name
          style
        }
        hero {
          publicURL
        }
        postDate
        stylePath{
          relativePath
        }
      }
    }
  }
`